<template>
  <div id="app">
    <div id="content">
     <vue-waterfall-easy :imgsArr="imgsArr" @scrollReachBottom="fetchImgsData"></vue-waterfall-easy>
    </div>
  </div>
</template>

<script>
import vueWaterfallEasy from 'vue-waterfall-easy'
export default {
  components: {
    vueWaterfallEasy
  },
  props:{
      gap: { // 
        type: Number,
        default: 20
      },
//    imgWidth: { // 
  //    type: Number,
    //  default: 240
    //},
    timeOut: { // 500
      type: Number,
      default: 500
    },
    height:{
         type: String,
          default: '100%'
    },
  },
  name: 'app',
 data () {
     return {
    			imgsArr:[],
    			 fetchImgsArr: []    //
    		}
    	},
    	methods:{
    		initImgsArr (n, m) {   //
    	var arr = []
    	for (var i = n; i < m; i++) {
    	arr.push({id:i, src: `./src/assets/img/${i + 1}.jpg`, link: 'https:/', info: '' })

    			//srclink
                  //info
    	}
    	return arr
    	},
    		fetchImgsData () {    //
            this.imgsArr = this.imgsArr.concat(this.fetchImgsArr)   //
        }
    	},
    	created () {
    this.imgsArr = this.initImgsArr(0, 8)       //
    this.fetchImgsArr = this.initImgsArr(8, 16) // 
    }
}
</script>

<style lang="scss">
#app, body, html {
    height: 100%;
}
* {
    margin: 0;
    padding: 0;
}
#app {
    overflow: auto;
    position: relative;
}
#app #content {
    position: absolute;
    top: 32px;
    bottom: 0;
    width: 100%;
}
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
.vue-waterfall-easy {
  position: relative;
  width: 100%;

  }
  .img-box {
    display: inline-block;
    width: 50%;
    box-sizing: border-box;
    float: left;
    transition: left 1s, top 1s;
}
    .img-inner-box {
      box-shadow: 0 1px 3px rgba(0, 0, 0, .3);

    }
	 .img-wraper {
        width: 100%;
        background: yellow;
      }
      img {
        width: 100%;
        vertical-align: bottom;
      }
.vue-waterfall-easy-container   .vue-waterfall-easy-scroll{

                    overflow-y: auto !important;
            }

</style>



